@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    overflow-x: hidden;
}

#backg {
    display: block;
    width: 100%;
  }

/*--- Navigation --*/
.navbar {
    text-transform: uppercase;
    font-weight: 700;
    font-size: .9rem;
    letter-spacing: .15rem;
}
nav.navbar {
background: rgba(0,0,0,0.6)!important;
}
.navbar-brand {
color: #32b5e9!important;
padding-left: .7rem;
}
.navbar-nav li {
padding-right: .7rem;
padding-left: .7rem;
}
.navbar-dark .navbar-nav .nav-link {
color: #fff!important;
padding-top: .8rem;
}
.navbar-dark .navbar-nav .nav-link:active,
.navbar-dark .navbar-nav .nav-link:focus,
.navbar-dark .navbar-nav .nav-link:hover {
color: #32b5e9!important;
}
.offset {
    margin-top: 4rem;
}


/*-- Landing --*/
.carousel-inner img {
    width: 100%;
    height: auto;
}
.landing .carousel-caption {
    height: 100%;
    width: 100%;
    left: 0%;
    right: 0%;
    padding-top: 8rem;

  }

/*--- Cards --*/
.card {
    margin: 1rem;
    background-color: #f2f2f2;
    color: black;
    box-shadow: 0 0 16px black;
}

.card .card-body {
    padding: 0;
}

.card h5 {
    color: black;
    font-size: 1.6rem;
    font-weight: 700;
    padding-top: 1rem;
    margin-bottom: .2rem;
}
.card-category {
    display: inline-block;
    margin: .2rem!important;
    padding: .2rem .6rem!important;
    background-color: coral;
    color: cornsilk;
    border-radius: 13px;
}
.card-category p{
    display: inline;
    font-size: .9rem;
}
.card-category i{
    display: inline;
    color: black;
    padding-right: .4rem;
}
.card-text {
    margin-bottom: .2rem;
}


.jumbotron {
    background: rgba(0,0,0,0.6)!important;
    height: 200px;
    padding-top: 4.4rem;
}
.jumbotron .input-group {
    padding: 0 2rem;
}

.btn-outline-secondary {
    border: 2px solid black;
    padding: .3rem 5rem!important;
    color: black;
    font-weight: 700;
    font-size: 1.2rem;
    letter-spacing: .1rem;
    margin-bottom: .4rem;
    box-shadow: 0 0 2px black;
}
.btn-outline-secondary:hover {
    border: 2px solid #32b5e9;
    background-color: #32b5e9;
    box-shadow: 0 0 2px #32b5e9;
    color: black;

}

